	import divider from '@/components/divider.vue';
	import freeAvater from '@/components/free-avater.vue'
	import uniLoadMore from '@/components/uni-load-more/uni-load-more.vue'
	//引入网络请求
	import $H from '@/common/request.js';
	import $C from '@/common/config.js';
	import $U from '@/common/util.js';
	
	export default {
		components: {
			divider,
			freeAvater,
			uniLoadMore
		},

		data() {
			return {
				background: {
					backgroundColor: '#fff',
				},
				checked: true,
				params: {
					year: false,
					month: true,
					day: true,
					hour: false,
					minute: false,
					second: false
				},
				show: false,
				month: "",
				day: "",
				year: "",
				month2: "",
				day2: "",
				year2: "",
				nowTime: "",
				searchDayTime:"",
				searchMonthTime:"",
				dataList:[],
				page:1,
				reload: true,
				loadMore: false,
				nodata:false,
				status: 'more',
				contentText: {
					contentdown: '查看更多',
					contentrefresh: '加载中',
					contentnomore: '没有更多'
				}
			}
		},

		computed: {
           
		},

		mounted() {
			var date = new Date();
			this.month = date.getMonth() + 1;
			if (this.month < 10) {
				this.month = "0" + (date.getMonth() + 1).toString() + "月";
			} else {
				this.month = (date.getMonth() + 1).toString() + "月";
			}
			this.year = date.getFullYear().toString() + "年"
			this.day = date.getDate().toString() + "日";
			this.nowTime = this.month + this.day;
			
			if (this.month2 < 10) {
				this.month2 = "0" + (date.getMonth() + 1).toString() ;
			} else {
				this.month2 = (date.getMonth() + 1).toString() 
			}
			
			if (this.day2 < 10) {
				this.day2 = "0" + date.getDate().toString() ;
			} else {
				this.day2 = date.getDate()().toString() 
			}
			
			this.year2 = date.getFullYear().toString() 
			this.searchDayTime=this.year2+"-"+this.month2 +"-"+this.day2;
			
			this.getDayData()
			console.log(this.day2)
		},

       onReachBottom() {
		this.loadMore = true;
		this.page++;  
		if (this.status == 'noMore') {
			return;
		}
		this.status = 'loading';
       	if(this.checked){
			this.getDayData()
		}
		else{
			this.getMounthData()
		}
       },


		methods: {
			// 切换日/月报
			chooseType(index) {
				if (index == 1) {
					this.page=1
					this.reload = true;
					this.checked = true;
					this.params.year = false
					this.params.day = true
					this.nowTime = this.month + this.day;
					this.searchDayTime=this.year2+"-"+this.month2 +"-"+this.day2;
					this.status = 'loading';
					this.getDayData()
				} else {
					this.page=1
					this.reload = true;
					this.checked = false;
					this.params.year = true
					this.params.day = false
					this.nowTime = this.year + this.month;
					this.searchMonthTime=this.year2 +"-"+this.month2+"-01";
					this.status = 'loading';
					this.getMounthData()
				}

			},

			// 切换选择器
			choosePicker() {
				this.show = true
			},

			// 选择时间
			chooseTime(res) {
				console.log(res)
				if (this.checked) {
					this.nowTime = res.month + "月" + res.day + "日";
					this.searchDayTime=this.year2+"-"+res.month+"-"+res.day
					this.page=1
					this.reload = true;
					this.getDayData()
				} else {
					this.nowTime = res.year + "年" + res.month + "月";
					this.searchMonthTime=res.year+"-"+res.month+"-01"
					this.page=1
					this.reload = true;
					this.getMounthData()
				}
			},
			
			// 获取列表数据
			getDayData(){
				
				uni.showLoading({
					title: '加载中'
				});
				
				let param={
					size:10,
					current:this.page,
				}
				
				// 发送到服务端
				$H.get("/order/today?searchDayTime="+this.searchDayTime,param).then((res) => {
					
					uni.hideLoading();
					//请求成功
					let list = res.data.orderList.records
					
					let pages=res.data.orderList.pages;
					
					
					if(pages>1&&this.page<=pages){
						this.dataList = this.reload ? list : this.dataList.concat(list);
						this.nodata=false;
					}
					else if(pages==0){
						this.dataList=[];
						this.nodata=true;
						this.status ='noMore'
					}
					else if(pages==1){
						this.dataList = list
						this.status ='noMore'
						this.nodata=false;
					}
					else{
						this.status ='noMore'
					}
					console.log(this.dataList.length)
					
					this.reload = false;
					
				}).catch((e) => {
					
					//请求失败
					console.log("失败"+e)
				})
			},
			
			getMounthData(){
				uni.showLoading({
					title: '加载中'
				});
				
				let param={
					size:10,
					current:this.page,
				}
				
				// 发送到服务端
				$H.get("/order/month?searchMonthTime="+this.searchMonthTime,param).then((res) => {
					//请求成功
				uni.hideLoading();
				//请求成功
				let list = res.data.orderList.records
				let pages=res.data.orderList.pages;
				
				
				if(pages>1&&this.page<=pages){
					this.dataList = this.reload ? list : this.dataList.concat(list);
					this.nodata=false;
				}
				else if(pages==0){
					this.dataList=[];
					this.nodata=true;
					this.status ='noMore'
				}
				else if(pages==1){
					this.dataList = list
					this.nodata=false;
					this.status ='noMore'
				}
				else{
					this.status ='noMore'
				}
				console.log(this.dataList)
				
				this.reload = false;
					
				}).catch((e) => {
					
					//请求失败
					console.log("失败"+e)
				})
			}
			
		}
	}
